.header__link {
  position: relative;

  display: block;
  padding: 10px 15px;
  padding-left: 40px;

  font-family: $PT_MONO;
  font-style: normal;
  font-weight: normal;
  line-height: 18px;
  color: $WHITE_COLOR;
  font-size: 16px;

  transition: all 0.3s ease-out;

  svg {
    position: absolute;

    fill: $SKY_BLUE_COLOR;
  }

  &--email svg {
    top: 12px;
    left: 1px;

    width: 19px;
    height: 14px;
  }

  &--tel svg {
    top: 10px;
    left: 5px;

    width: 18px;
    height: 18px;
  }

  &:hover,
  &:focus {
    color: $SKY_BLUE_COLOR;
  }

  &:hover svg,
  &:focus svg {
    fill: $WHITE_COLOR;
  }

  &:active {
    opacity: 0.7;
  }
}

@media (max-width: $desktop-width) {
  .header__link {
    font-size: 0;

    &--email,
    &--tel {
      padding-left: 0;
    }

    &--email {
      width: 25px;
      height: 18px;
    }

    &--tel {
      width: 24px;
      height: 24px;
    }

    svg {
      top: 0;
      left: 0;
    }

    &--email svg {
      width: 25px;
      height: 18px;
    }

    &--tel svg {
      width: 24px;
      height: 24px;
    }
  }
}
